<template>
    <div class="playoff">
      <span>我的复赛</span>
      <el-form v-show="!isShow" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

        <el-form-item label="视频名称"  size="mini" style="width: 400px">
          <!--<el-input v-model="ruleForm.name"></el-input>-->
          <!--<span>[复赛视频]{{ $store.state.preliminary.preliminary1.school }}-{{ $store.state.preliminary.preliminary1.name}}-{{ $store.state.preliminary.preliminary1.call.slice(-4)}}</span>-->
          <span style="font-size: 12px">{{recsnameMsg}}</span>
        </el-form-item>

        <el-form-item label="视频地址" prop="address" size="mini" style="width: 400px;margin-top: 30px">
          <el-input v-model="ruleForm.address" placeholder="请输入教学视频分享地址（百度云盘或个人网站）"></el-input>
        </el-form-item>

        <el-form-item label="分享密码"  size="mini" style="width: 400px;margin-top: 30px">
          <el-input v-model="ruleForm.pass" placeholder="分享密码（如无密码可不填写）"></el-input>
        </el-form-item>

        <p style="color: #525151">
          格式：MP4/RMVB/MKV/AVI ；时长：不超过25分钟；大小：不超过400M
          请将教学视频上传至百度云并开放分享,上传的视频文件名称须同视频名称一致
          咨询电话：010-65258730
        </p>

        <el-form-item>
          <el-button type="primary" @click="sub('ruleForm')" style="width: 200px;background-color: #f76c1a;border: #f76c1a;margin-top: 30px">提交</el-button>
        </el-form-item>
      </el-form>


      <playoff2 :val='this.ruleForm' v-if="isShow"></playoff2>
    </div>
</template>

<script>
    import store from '../vuex/store'
    import playoff2 from '../components/Playoff2'

    export default {
      name: "playoff",
      data() {
        return {
          ruleForm: {
            address:'',
            pass:'',
            recsnameMsg:'',
            grade:''  //几等奖
          },
          rules: {
            address: [
              {required: true, message: '请输入视频地址', trigger: 'blur'}
            ],
          },
          isShow:false
        };
      },
      methods:{
          sub(){
            this.isShow=true
            // console.log(this.ruleForm);

            this.$post('/levy/repecharge/recourse',{
              id:this.$store.state.id,
              videoURL:this.ruleForm.address,
              vipwd:this.ruleForm.pass
            }).then(res => {
              console.log(res);
            })

          }
      },
      components:{
        playoff2
      },
      created(){
        console.log(this.$route.params.performance);
        this.ruleForm.grade = this.$route.params.performance
        this.recsnameMsg = this.$route.params.recsname

      },
      store

    }
</script>

<style scoped lang="scss">

  .playoff{
    padding:  28px;
    background-color: #fff;
    span{
      font-size: 18px;
    }
    form{
      margin-top: 40px;
      p{
        width: 400px;
        font-size: 12px;
        color: #d1d1d1;
        margin-left: 32px;
        margin-top: -8px;
      }
    }
  }
</style>



