<template>
    <div class="playoff2">

      <div style="margin-top: 10px">
        <img src="../assets/caijian.png" alt="">
        <span style="color: #000;font-weight: normal">{{msg}}</span>
      </div>

      <div class="fon" style="margin-top: 50px;margin-left: -10px">
        <span>视频名称：</span>
        <span>{{videoName}}</span>
      </div>

      <div class="fon" style="margin-top: 25px;position: relative;margin-left: -10px">
        <span>视频地址：</span>
        <a :href="videoaddress" style="position: absolute;top: -20px;left:73px">{{videoaddress}}</a>
      </div>

      <div class="fon" style="margin-top: 25px;margin-left: -10px">
        <span>分享密码：</span>
        <span>{{pass}}</span>
      </div>
    </div>
</template>

<script>
    export default {
        name: "playoff2",
        data(){
          return {
            msg:'',
            videoName:'',
            videoaddress:' ',
            pass:'',
          }
        },
        props:{
          val:{
            type:Object
          }
        },
        created(){

          this.videoaddress = this.val.address
          this.pass = this.val.pass
          this.msg = this.val.grade
        },

    }
</script>

<style scoped lang="scss">
  .playoff2{
    padding:  28px;
    background-color: #fff;
    span{
      font-size: 18px;
    }
    div{
      img{
        width: 40px;
        height: 40px;
      }
      span{
        position: relative;
        top: -20px;
        left: 10px;
        font-size: 12px;
        font-weight: bold;
        color: #55a532;
      }
    }
    .fon{
      span{
        color: #000;
        font-weight: normal;
      }
      a{
        text-decoration: underline;
        color: #f76c1a;
      }
    }
  }
</style>



